/* âœ… Chrome autofill - make text visible */
.mui-autofill-fix input:-webkit-autofill,
.mui-autofill-fix input:-webkit-autofill:hover,
.mui-autofill-fix input:-webkit-autofill:focus,
.mui-autofill-fix input:-webkit-autofill:active {
  -webkit-text-fill-color: var(--bs-primary2) !important;
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important; /* input background */
  transition: background-color 5000s ease-in-out 0s;
}